<template>
  <v-app>
    <section class="auth-section">
      <div>
        <div class="text-center mb-4">
          <h1 class="c-dark">Welcome Back !</h1>
        </div>
        <div class="auth-card">
          <!-- Social Buttons -->
          <v-btn depressed color="black" dark block large>
            <v-icon left>mdi-apple</v-icon>
            Sign in with Apple</v-btn
          >

          <v-btn class="mt-5" depressed color="blue" dark block large>
            <v-icon left>mdi-google</v-icon>
            Sign in with Google</v-btn
          >

          <div class="divider-chip">
            <span>or use email</span>
          </div>

          <!-- Email Form -->
          <p class="field-label">Email Address</p>
          <v-text-field
            hide-details=""
            outlined
            placeholder="your.name@email.com"
          ></v-text-field>

          <p class="field-label mt-4">Password</p>
          <v-text-field
            :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
            :type="show1 ? 'text' : 'password'"
            @click:append="show1 = !show1"
            placeholder="••••••••••••"
            outlined
            hide-details=""
          ></v-text-field>
          <div class="text-end mt-4">
            <router-link :to="{ name: 'forgotPassword' }"
              >Forgot Password?</router-link
            >
          </div>
          <v-btn
            class="mt-3"
            large
            depressed
            color="primary"
            :loading="loader"
            :disabled="loader"
            @click="goToDashboard"
