<script>
export default {
  data: () => ({
    show1: false,
    loader: false,
  }),

  methods: {
    goToDashboard() {
      this.loader = true;
      setTimeout(() => {
        this.loader = false;
        this.$router.push({ name: "dashboard" });
      }, 3000);
    },
  },
};
</script>
