import Vue from "vue";
import VueRouter from "vue-router";

import App from "../views/App.vue";

// Frontend Routes
import LandingPage from "../views/frontend/LandingPage.vue";

// Admin Routes
import AdminLogin from "../views/admin/auth/Login.vue";
import ForgotPassword from "../views/admin/auth/ForgotPassword.vue";
import Main from "../views/admin/Main.vue";
import Dashboard from "../views/admin/main/Dashboard.vue";
import Appointments from "../views/admin/main/Appointments.vue";


Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "app",
    component: App,
    children: [
      {
        path: "/",
        name: "landingPage",
        component: LandingPage,
      },

      // For Admin
      {
        path: "/admin-login",
        name: "adminLogin",
        component: AdminLogin,
      },
      {
        path: "/forgot-password",
        name: "forgotPassword",
        component: ForgotPassword,
      },
      {
        path: "/admin",
        name: "admin",
        component: Main,
        children: [
          {
            path: "/admin/dashboard",
            name: "dashboard",
            component: Dashboard,
          },
          {
            path: "/admin/appointments",
            name: "appointments",
            component: Appointments,
          },
        ],
      },
    ],
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
